    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    z-index: 1;
    margin-bottom: 50px;
}

.about-container {
    display: flex;
    height: 100%;  
    width: 100%;
    font-family: 'Rubik', sans-serif;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.about-information {
    background: white;
}

.about-me-title {
    height: 50px;
    width: 100%;
    border-bottom: 1px solid white;
    background: #00264d;
    color: white;
