}

.about-contact-me-title {
    display: flex;
    align-items: center;
    text-align: center;
    font-style: italic;
    font-size: 18px;
}

.about-contact-me-title:before,
.about-contact-me-title:after {
  background: black;
  height: 1px;
  flex: 1 1;
  content: '';
}

.about-contact-me-title-text {
    padding-right: 10px;
    padding-left: 10px;
}

.about-me-information-one {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    text-align: left;
}

.about-me-photo {
    width: 100%;
    height: auto;
    align-self: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

