#herosection {
    height: 100vh;
    position: relative;
    z-index: 1;
    margin-bottom: 200px;
    overflow: hidden;
}

h1, h4 {
    margin: 0;
}

.hero-container {
    position: absolute;
    top: 50%; left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: 'Roboto Slab', serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 50%;
    justify-content: space-evenly;
    z-index: 1;
}

.rocket-icon {
  position: absolute;
  height: auto;
  width: 50%;
  bottom: 2%;
  right: -15%;
}

.code-icon {
  position: absolute;
  height: auto;
  width: 50%;
  top: 0%;
  left: -15%;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  margin: 0;
}

.gamecontroller-icon {
  position: absolute;
  height: auto;
  width: 50%;
  bottom: 2%;
  left: -15%;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
}

.pizza-icon {
  position: absolute;
  height: auto;
  width: 50%;
  top: 0%;
  right: -15%;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}

.hero-title {
    color: #00264d;
    text-align: center;
    margin: 0;
    font-size: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    letter-spacing: 1px;
}

.hero-title h1 {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;    
  animation: fadeIn linear 1s;
  -webkit-animation: fadeIn linear 1s;
  -moz-animation: fadeIn linear 1s;
  -o-animation: fadeIn linear 1s;
  -ms-animation: fadeIn linear 1s;
}

.hero-hi {
    color: #262626;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;    
    animation: fadeIn linear 1s;
    -webkit-animation: fadeIn linear 1s;
    -moz-animation: fadeIn linear 1s;
    -o-animation: fadeIn linear 1s;
    -ms-animation: fadeIn linear 1s;
}

.hero-motto {
    font-size: 30px;
    text-align: center;
    color: #00264d;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    animation: fadeIn linear 1s;
    -webkit-animation: fadeIn linear 1s;
    -moz-animation: fadeIn linear 1s;
    -o-animation: fadeIn linear 1s;
    -ms-animation: fadeIn linear 1s;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

.hero-icons-container {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.hero-icon {
    height: 60px;
    width: auto;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
}

.hero-icon:hover {
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.break-one {
    display: none;
}

.break-two {
  margin: 15px 0px 15px;
  height: 1px;
  background: black;
  width: 30%;
  border: none;
}

.hero-subsection {
  display: flex;
  flex-direction: column;
  align-items: center;
}

